@charset "UTF-8";
@import "../../../style/theme/default/var.css";
body{

  background: #0f62c7;
}
.login{
  width:100%;
  height:100%;
  background: #0f62c7;
}
@component-namespace login{
  @b header{
    padding:20px 30px;
  }
  @b container {
    position:relative;
    z-index:2;
  @utils-clearfix;
    width:1100px;
    margin: 2% auto 0;
    @e left{
      text-align:center;
      float: left;
      width: 600px;
      color:var(--color-white);
      & > h3{
      font-size: 32px;
      margin-top:0;
          }
      & > h5 {
          color: #73a7fd;
          margin-bottom: 30px;
          font-size:var(--theme-font-size-base);
        }
      & .login-ad img{
      max-width: 100%;
        }
    }
    @e right{
      float: left;
      width: 500px;
    }
  }

  @b box {
    width: 364px;
    min-height: 420px;
    margin: 40px auto 0;
    background: #fff;
    border-radius: 10px;
    padding: 70px 27px;
    position: relative;
    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      top:0;
      z-index:0;
      width: 37px;
      height:100%;
      background-image: url('data:image/png;base64,');
      background-repeat: no-repeat;
    }
    &:before{
      left:-37px;
       transform: rotateY(180deg);
       -webkit-transform: rotateY(180deg);
     }
    &:after{
      right:-37px;
     }
  }

  @b logo {
    text-align: center;
    & span{
      display: inline-block;
      width:90px;
      height:90px;
      border-radius: 50%;
      background: #0f62c7;
      line-height: 88px;
    }
  }

  @b form{
    box-sizing: border-box;
    border:1px solid #e0e0e0;
    border-radius: 3px;
    padding: 5px 20px;
    margin: 30px 0;
    & .login-form__item{
      position: relative;
      display:block;
      margin-bottom: 0;
     &.broder{
        border-bottom: 1px solid #e0e0e0;
      }
     & i {
        font-size:var(--theme-font-size-middle);
        color: var(--theme-font-color-secondary);
       }
    .el-input-group__prepend{
      border:0;
    }
    &  input {
       border: 0;
       box-sizing: border-box;
       color: var(--theme-font-color-primary);
       font-size: var(--theme-font-size-base);
       height: 40px;
       outline: none;
       width: 100%;
        &::placeholder {
           color: var(--theme-font-color-secondary);
        }
     }
    }
    .el-form-item {
      margin-bottom: 0;
    }
  }

  @b btn {
    .el-button {
      display: block;
      width: 100%;
      padding: 16px 20px;
      border-radius: 3px;
      font-size:var(--theme-font-size-base);
    }
  }
  @b footer{
    position: fixed;
    z-index:1;
    left:0;
    bottom:0;
    width:100%;
    text-align: center;
    color: #73a7fd;
    font-size: var(--theme-font-size-base);
    text-align: center;
    padding:20px 30px;
    background: #0f62c7;
  }
}

@media (max-width: 1300px) {

}



